@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri:wght@300&display=swap);
/* Importing font from google font */

/* CSS reset */
*,
*::before,
*::after {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
html {
  max-width: 1600px;
  min-width: 200px;
  font-size: 13px;
  font-family: 'Hind Siliguri', sans-serif;
}
body{
  background-image: linear-gradient(145deg,var(--shade3),var(--shade2));
}

/* Container is the parent of all the element of the web page after body */
.container {
  width: 60%;
  height: 100vh;
  margin: 0em auto 0 auto;
  font-size: 1.2em;
  background-image: url('https://cdn.pixabay.com/photo/2017/03/02/16/54/iceland-2111811_1280.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  box-shadow: 1px 0 5px rgb(0, 0, 0);
}
/* profile contain profile pic and name . And show at the top of the chat window */
.profile {
  width: 100%;
  padding: 0.5em;
  background-color: rgb(228, 213, 241);
  box-shadow: 0 2px 5px black;
  display: flex;
  align-items: center;
}
.profile img {
  width: 3em;
  margin: 0 0.5em 0 0.5em;
}
.profile-name {
  color: red;
  text-align: center;
  font-size: 1.3em;
}

/* messgae is the main container of messages.  */
.messages {
  width: 100%;
  height: calc(100% - 7.5em);
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  
}
/* inhert is a empty div only used to inhert the height of Reactautoscrollfeed component */
.inhert{
  height: 100%;
}

/* These codes are for scrollbar design */
.messages::-webkit-scrollbar {
  width: 0.2em;
}
.messages::-webkit-scrollbar-thumb {
  background: rgb(204, 204, 204);
  border-radius: 10px;
}

/* It contains the image, message and time */
.msg-container {
  display: flex;
  flex-direction: column;
}
.image {
  width: 2em;
  height: 2em;
  margin: 0.5em;
  display: inline-block;
  border-radius: 90px;
}
.user {
  align-items: flex-end;
}

/* msg-line contains message and small image */
.msg-line {
  display: flex;
  flex-direction: row;
}
p {
  margin: 0.5em;
  padding: 0.5em;
  background-color: rgb(150, 150, 150);
  border-radius: 10px;
  box-shadow: 1px 1px 2px black;
}
.user p {
  color: white;
  background-color: rgb(54, 21, 129);
}
.time{
  color: rgb(223, 223, 223);
  /* font-size: 0.3em; */
}
.agent .time {
  margin-left: 4em;
}
.user .time {
  margin-right: 4em;
}

.user > .direction {
  flex-direction: row-reverse;
}

/* input-btn is a form that contain input field where user will type message and button which end the message */
.input-btn {
  display: flex;
}
.input-btn > input,
button {
  margin: 0.3em;
  padding: 0.3em;
  font-size: 0.9em;
  border-radius: 15px;
  outline: none;
}
.input-btn > input {
  width: 80%;
  padding: 0.8em;
  border: none;
  background-color: rgb(221, 221, 221);
}
.input-btn > button {
  width: 16.5%;
  background-color: red;
  border: none;
  color: white;
  min-width: 3em;
  transition: ease-in-out 0.2s;
}
.input-btn > button:hover {
  background-color: brown;
  cursor: pointer;
}

.notify-msg{
  color: white;
  background-color: transparent;
  text-align: center;
}

/* after 770px chat window will be full screen. In Ipad full screen view will be shown */
@media screen and (max-width: 770px) {
  .container {
    width: 100%;
    margin: 0%;
  }
}




:root {
	--fg: #17181c;
	--shade1: #727274;
	--shade2: #cccdd1;
	--shade3: #f3f4f8;
	--shade4: #ffffff;
	--dur1: 1s;
	--dur2: 6s;
}
.loader-container {
    font-size: calc(16px + (32 - 16)*(100vw - 320px)/(2560 - 320));
	background-image: linear-gradient(145deg,#f3f4f8,#cccdd1);
	background-image: linear-gradient(145deg,var(--shade3),var(--shade2));
	color: #17181c;
	color: var(--fg);
	display: flex;
	/* font: 1em/1.5 Hind, sans-serif; */
	flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 100;
}
.preloader {
	-webkit-animation: largePopOut 1s linear;
	        animation: largePopOut 1s linear;
	-webkit-animation: largePopOut var(--dur1) linear;
	        animation: largePopOut var(--dur1) linear;
	border-radius: 50%;
	box-shadow:
		0.15em 0.15em 0.15em #ffffff inset,
		-0.15em -0.15em 0.15em #727274 inset,
		1em 1em 2em #727274, 
		-1em -1em 2em #ffffff;
	box-shadow:
		0.15em 0.15em 0.15em var(--shade4) inset,
		-0.15em -0.15em 0.15em var(--shade1) inset,
		1em 1em 2em var(--shade1), 
		-1em -1em 2em var(--shade4);
	margin-bottom: 3em;
	position: relative;
	width: 12em;
	height: 12em;
    opacity: 1;
}
.preloader__square {
	-webkit-animation: smallPopOut1 1s linear, popInOut 6s 1s linear infinite;
	        animation: smallPopOut1 1s linear, popInOut 6s 1s linear infinite;
	-webkit-animation: smallPopOut1 var(--dur1) linear, popInOut var(--dur2) var(--dur1) linear infinite;
	        animation: smallPopOut1 var(--dur1) linear, popInOut var(--dur2) var(--dur1) linear infinite;
	border-radius: 0.5em;
	box-shadow:
		0.15em 0.15em 0.15em #ffffff inset,
		-0.15em -0.15em 0.15em #727274 inset,
		0.25em 0.25em 0.5em #727274,
		-0.25em -0.25em 0.5em #ffffff;
	box-shadow:
		0.15em 0.15em 0.15em var(--shade4) inset,
		-0.15em -0.15em 0.15em var(--shade1) inset,
		0.25em 0.25em 0.5em var(--shade1),
		-0.25em -0.25em 0.5em var(--shade4);
	position: absolute;
	top: 2.5em;
	left: 2.5em;
	width: 3em;
	height: 3em;
}
.preloader__square:nth-child(n + 2):nth-child(-n + 3) {
	left: 6.5em;
}
.preloader__square:nth-child(n + 3) {
	top: 6.5em;
}
.preloader__square:nth-child(2) {
	-webkit-animation: smallPopOut2 1s linear, move2 6s 1s linear infinite;
	        animation: smallPopOut2 1s linear, move2 6s 1s linear infinite;
	-webkit-animation: smallPopOut2 var(--dur1) linear, move2 var(--dur2) var(--dur1) linear infinite;
	        animation: smallPopOut2 var(--dur1) linear, move2 var(--dur2) var(--dur1) linear infinite;
}
.preloader__square:nth-child(3) {
	-webkit-animation: smallPopOut3 1s linear, move3 6s 1s linear infinite;
	        animation: smallPopOut3 1s linear, move3 6s 1s linear infinite;
	-webkit-animation: smallPopOut3 var(--dur1) linear, move3 var(--dur2) var(--dur1) linear infinite;
	        animation: smallPopOut3 var(--dur1) linear, move3 var(--dur2) var(--dur1) linear infinite;
}
.preloader__square:nth-child(4) {
	-webkit-animation: smallPopOut4 1s linear, move4 6s 1s linear infinite;
	        animation: smallPopOut4 1s linear, move4 6s 1s linear infinite;
	-webkit-animation: smallPopOut4 var(--dur1) linear, move4 var(--dur2) var(--dur1) linear infinite;
	        animation: smallPopOut4 var(--dur1) linear, move4 var(--dur2) var(--dur1) linear infinite;
}
.status {
	-webkit-animation: fadeIn 1s linear forwards;
	        animation: fadeIn 1s linear forwards;
	-webkit-animation: fadeIn var(--dur1) linear forwards;
	        animation: fadeIn var(--dur1) linear forwards;
	text-align: center;
}
.status__dot {
	-webkit-animation: appear1 1s 1s steps(1,start) infinite;
	        animation: appear1 1s 1s steps(1,start) infinite;
	-webkit-animation: appear1 var(--dur1) var(--dur1) steps(1,start) infinite;
	        animation: appear1 var(--dur1) var(--dur1) steps(1,start) infinite;
	display: inline-block;
}
.status__dot:nth-child(2) {
	-webkit-animation: appear2 1s 1s steps(1,start) infinite;
	        animation: appear2 1s 1s steps(1,start) infinite;
	-webkit-animation: appear2 var(--dur1) var(--dur1) steps(1,start) infinite;
	        animation: appear2 var(--dur1) var(--dur1) steps(1,start) infinite;
}
.status__dot:nth-child(3) {
	-webkit-animation: appear3 1s 1s steps(1,start) infinite;
	        animation: appear3 1s 1s steps(1,start) infinite;
	-webkit-animation: appear3 var(--dur1) var(--dur1) steps(1,start) infinite;
	        animation: appear3 var(--dur1) var(--dur1) steps(1,start) infinite;
}


